<template>
  <div v-theme:column="'narrow'" id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索">
    <div v-for="blog in filteredBlogs" class="single-blog">
    	<!-- 当点击这条信息的时候就跳转到那条信息 -->
      <router-link v-bind:to="'/blog/' + blog.id">
       <!--  | to-uppercase是把标题都变成大写的 |后面就是表示添加一个过滤器，过滤器的名字
       过滤器在main中定义-->
        <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
      </router-link>

    	<article>
    		{{blog.content | snippet}}
    	</article>
    </div>
  </div>
</template>

<script>

export default {
  name: 'show-blogs',
  data(){
  	return {
  		blogs:[],
  		search:""
  	}
  },
  created(){
  	this.$http.get('https://dataexample-e22b7.firebaseio.com/posts.json')

    // 当get成功后用then方法，就是然后的意思
  		.then(function(data){
        // 返回取到的数据
        return data.json()
  			// console.log(data.json());
        //拿到0-10条数据 
  			// this.blogs = data.body.slice(0,10);
  			// console.log(this.blogs);
  		})
      .then(function(data){
        // 定义一个数组，因为请求过来的是对象，然后上面得到是数组，显示不了，所以就把对象存到数组
        var blogsArray = [];
        for(let key in data){
          // console.log(key);
          // 拿到每个对象
          // console.log(data[key]);
          data[key].id = key;
          // 把对象加入到shuzu
          blogsArray.push(data[key]);
        }
        // console.log(blogsArray);
        this.blogs = blogsArray;
        console.log(this.blogs);
      })
  },
  // 在vue如果有要改变什么的话，总是写在computed中
  computed:{
  	filteredBlogs:function(){
  		return this.blogs.filter((blog) =>{
  			// 匹配this.search（用户输入搜素的内容）
        return blog.title.match(this.search);
  		})
  	}
  },
  
 // 局部的过滤器啊的。。。。局部方法
  filters:{
  	// "to-uppercase":function(value){
  	// 	return value.toUpperCase();
  	// }
  	toUppercase(value){
  		return value.toUpperCase();
  	}
  },
  // 把指令变成局部的
  directives:{
  	'rainbow':{
  		bind(el,binding,vnode){
  			// el.style.color = "#" + Math.random().toString(16).slice(2,8);
  		}
  	}
  }
}
</script>

<style>
#show-blogs{
	max-width: 800px;
	margin: 0 auto;
}

.single-blog{
	padding: 20px;
	margin: 20px 0;
	box-sizing: border-box;
	background: #eee;
  border: 1px dotted #aaa;
}

#show-blogs a{
  color: #444;
  text-decoration: none;
}

input[type="text"]{
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}


</style>



