
<!-- 这是添加博客的组件 -->

<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <!-- 这样点击添加之后就变成flase,这个表单就不在显示 -->
    <form v-if="!submmited">
    <!-- <form> -->
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required />

      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>
      
      <!-- 博客分类 -->

      <!-- V-model双向绑定，这样在下面的东西里直接引用就可以显示 -->
      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label>Angular4</label>
        <input type="checkbox" value="Angular4" v-model="blog.categories">
      </div>


      <label>作者:</label>
      <!-- v-model="blog.author"也是选择谁就是谁，好让下面显示出来 -->
      <select v-model="blog.author">
        <option v-for="author in authors">
          {{author}}
        </option>
      </select>
      
      <!-- 触发事件，当点击添加。prevent阻止刷新页面 post是方法名，为了点击时能触发这个方法，在data下面写写方法，方法放在methods中post-->
      <button v-on:click.prevent="post">添加博客</button>
    </form>

    <div v-if="submmited">
      <h3>您的博客发布成功!</h3>
    </div>

<!-- 在下面显示，这时就用双向绑定的属性 -->
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题: {{blog.title}}</p>
      <p>博客内容:</p>
      <p>{{blog.content}}</p>
      <p>博客分类:</p>
      <ul>
        <li v-for="category in blog.categories">
          {{category}}
        </li>
      </ul>
      <p>作者: {{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
        title:"",
        content:"",
        categories:[],
        author:""
      },
      authors:["花猫","黑猫","白猫"],
      // 是否点击了提交
      submmited:false
    }
  },
  methods:{
    // post对应的方法，要实现对应的请求就要用到请求，那就要安装vue-resource，在终端 npm install vue-resource --save
    post:function(){

      // 安装完毕后，在main.js中导入，如下所示：
      // import VueResource from 'vue-resource'
      // Vue.use(VueResource)
      // 然后就可以使用请求了
      // posts.json是在这个数据库下面创建posts的json数据
      // this.blog是传过去的信息
      this.$http.post("https://dataexample-e22b7.firebaseio.com/posts.json",this.blog)
          .then(function(data){
            console.log(data);
            // 当点击添加时就为ture,然后上面对应的输入框的 什么就不在显示而显示的就只是双向绑定了的 那一栏
            this.submmited = true;
          });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
}

#add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

label{
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 8px;
}

textarea{
  height: 200px;
}

#checkboxes label{
  display: inline-block;
  margin-top: 0;
}

#checkboxes input{
  display: inline-block;
  margin-right: 10px;
}

button{
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

#preview{
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3{
  margin-top: 10px;
}

</style>



